import React from "react";
import { themeDefault } from "../../themeDefault"; 
import { ButtonGroup } from './ButtonGroup';
import { Button } from "../Button/Button";

export default {
  title: "Interaction Components/ButtonGroup",
  component: ButtonGroup,
  decorators: [
    (Story, { globals: { themeColor }}) => (
      <div style={{ 
        padding: '3rem 5rem', 
        backgroundColor: themeDefault.palette.pastel[themeColor].main,
        borderRadius: '20px' }}>
        <Story />
      </div>
    )
  ],
  parameters: {
    layout: 'centered'
  },
  argTypes: {
    variant: {
      control: { type: 'select' },
      options: [ 'flat', 'inset', 'outset' ]
    },
    rounded: {
      control: { type: 'boolean' },
      defaultValue: true,
    },
    buttonsArray: {
      control: {type: 'string' },
      defaultValue: ['Option One', 'Option Two', 'Option Three' ]
    },
    buttonType: {
      control: { type: 'select' },
      options: [ 'concave', 'convex', 'flat' ]
    },
    styling: {
      control: { type: 'object' },
    }
  }
}


const Template = (args, { globals: { themeColor }} ) => 
  <ButtonGroup {...{...args, themeColor: themeColor }}>
    {args.buttonsArray.map((button) => (
      <Button 
        variant={args.variant} 
        groupButton='true' 
        themeColor={themeColor} 
        label={button}/>
    ))}
  </ButtonGroup>; 

export const Flat = Template.bind({});;
Flat.args = {
  variant: 'flat'
}
export const Inset = Template.bind({});;
Inset.args = {
  variant: 'inset'
}
export const Outset = Template.bind({});
Outset.args = {
  variant: 'outset'
}


