import React from 'react';
import { themeDefault } from '../../themeDefault';
import './buttonGroup.css';

export const ButtonGroup = (props) => {
  let mainColor = themeDefault.palette.pastel[props.themeColor].main
  let lightColor = themeDefault.palette.pastel[props.themeColor].light
  let darkColor = themeDefault.palette.pastel[props.themeColor].dark
  let variant = props.variant
  let styling = {};
  if (variant === "concave") {
    styling = {
      backgroundImage: `linear-gradient(145deg, ${mainColor}, ${lightColor})`,
      boxShadow: `4px 4px 8px ${darkColor}, -4px -4px 8px ${lightColor}`,
      overflow: 'hidden'
    }
  } else if (variant === "convex") {
    styling = {
      backgroundImage: `linear-gradient(145deg, ${lightColor}, ${mainColor})`,
      boxShadow: `4px 4px 8px ${darkColor}, -4px -4px 8px ${lightColor}`,
    }
  } else if (variant === "flat") {
    styling = {
      boxShadow: `4px 4px 8px ${darkColor}, -4px -4px 8px ${lightColor}`,
    }
  } else if (variant === "inset") {
    styling = {
      background: mainColor, 
      boxShadow: `4px 4px 8px ${darkColor} inset, -4px -4px 8px ${lightColor} inset`,
    }
  } else if (variant === "outset") {
    styling = {
      background: mainColor, 
      boxShadow: `4px 4px 8px ${lightColor} inset, -4px -4px 8px ${darkColor} inset`,
    }
  } 
  let classNames = 'buttonGroup';
  if (props.rounded) {
    classNames = [classNames, 'rounded'].join(' ');
  }
  return (
    <div className={classNames} style={styling}>
      {props.children}
    </div>
  )
}
